:host {
  display: flex;
  flex-direction: column;
  margin: 0.5rem;
}
:host .file-table {
  width: 100%;
  border-collapse: collapse;
}
:host .file-table th,
:host .file-table td {
  padding: 0.5rem;
  text-align: left;
  border-bottom: 1px solid var(--mat-divider-color);
}
:host .file-table th {
  font-size: 1.5rem;
  font-weight: bold;
  background-color: var(--mat-table-header-container-background-color);
}
:host .file-table tr:hover {
  background-color: var(--background-hover);
}
:host .file-table .icon {
  width: 56px;
  min-width: 56px;
  text-align: center;
}
:host .file-table :where(th, td):not(.full) {
  width: 0;
  white-space: nowrap;
}
:host .file-table .actions {
  width: 48px;
  text-align: center;
}
